/* ════════════════════════════════════════════════════════════════════
   👨‍🎓 STUDENT INTERFACE - SUPREME ELITE DESIGN
   ════════════════════════════════════════════════════════════════════ */

:root {
  /* Aurora Borealis */
  --aurora-1: linear-gradient(135deg, 
    #667eea 0%, #764ba2 20%, #f093fb 40%, 
    #4facfe 60%, #00f2fe 80%, #667eea 100%
  );
  
  --mesh-aurora: 
    radial-gradient(at 0% 0%, hsla(253,100%,70%,0.4) 0px, transparent 50%),
    radial-gradient(at 50% 0%, hsla(225,100%,70%,0.4) 0px, transparent 50%),
    radial-gradient(at 100% 0%, hsla(340,100%,70%,0.4) 0px, transparent 50%),
    radial-gradient(at 0% 50%, hsla(189,100%,70%,0.3) 0px, transparent 50%),
    radial-gradient(at 100% 50%, hsla(290,100%,70%,0.3) 0px, transparent 50%),
    radial-gradient(at 0% 100%, hsla(355,100%,70%,0.4) 0px, transparent 50%),
    radial-gradient(at 50% 100%, hsla(140,100%,70%,0.4) 0px, transparent 50%),
    radial-gradient(at 100% 100%, hsla(260,100%,70%,0.4) 0px, transparent 50%);
}

/* ─────────────────────────────────────────────────────────
   🏠 STUDENT HOME - LEGENDARY DESIGN
   ───────────────────────────────────────────────────────── */

.student-home-supreme {
  position: relative;
  min-height: 100vh;
  overflow-x: hidden;
  z-index: 1;
}

/* Animated Mesh Background */
.student-home-supreme::before {
  content: '';
  position: absolute;
  inset: 0;
  background: var(--mesh-aurora);
  animation: aurora-wave 20s ease infinite;
  z-index: -1;
}

/* ─────────────────────────────────────────────────────────
   🎯 STUDENT HEADER
   ───────────────────────────────────────────────────────── */

.student-header-supreme {
  position: relative;
  z-index: 100;
  padding: var(--space-6) 0;
  background: rgba(255, 255, 255, 0.03);
  backdrop-filter: blur(30px) saturate(180%);
  border-bottom: 1px solid rgba(255, 255, 255, 0.1);
  box-shadow: 
    0 10px 30px rgba(0,0,0,0.1),
    inset 0 0 0 1px rgba(255,255,255,0.05);
}

.student-header-content {
  max-width: 1600px;
  margin: 0 auto;
  padding: 0 var(--space-8);
  display: flex;
  justify-content: space-between;
  align-items: center;
  gap: var(--space-6);
}

.student-logo-supreme {
  display: flex;
  align-items: center;
  gap: var(--space-4);
}

.logo-icon-supreme {
  width: 60px;
  height: 60px;
  background: var(--aurora-1);
  background-size: 200% 200%;
  border-radius: var(--radius-xl);
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 2rem;
  color: white;
  box-shadow: var(--glow-primary);
  animation: gradient-shift 8s ease infinite,
             breathing-glow 3s ease-in-out infinite;
}

.logo-text-supreme {
  font-size: 1.75rem;
  font-weight: 900;
  font-family: var(--font-display);
  background: linear-gradient(135deg, #ffffff 0%, #e0e7ff 100%);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  background-clip: text;
  text-shadow: 0 4px 20px rgba(0,0,0,0.3);
  letter-spacing: -0.02em;
}

.student-user-section-supreme {
  display: flex;
  align-items: center;
  gap: var(--space-6);
}

.student-points-badge-supreme {
  position: relative;
  display: flex;
  align-items: center;
  gap: var(--space-3);
  padding: var(--space-3) var(--space-6);
  background: rgba(255, 255, 255, 0.08);
  backdrop-filter: blur(20px);
  border: 1px solid rgba(255, 255, 255, 0.15);
  border-radius: 50px;
  box-shadow: 
    0 8px 24px rgba(0,0,0,0.15),
    inset 0 0 0 1px rgba(255,255,255,0.1);
  overflow: hidden;
  transition: all 0.4s var(--ease-smooth);
}

.student-points-badge-supreme::before {
  content: '';
  position: absolute;
  top: 0;
  left: -100%;
  width: 100%;
  height: 100%;
  background: linear-gradient(90deg, transparent, rgba(255,255,255,0.2), transparent);
  transition: left 0.6s;
}

.student-points-badge-supreme:hover::before {
  left: 100%;
}

.student-points-badge-supreme:hover {
  transform: translateY(-2px);
  box-shadow: 
    0 12px 32px rgba(0,0,0,0.2),
    var(--glow-gold);
  border-color: rgba(251,191,36,0.3);
}

.points-icon {
  font-size: 1.75rem;
  filter: drop-shadow(0 0 8px rgba(251,191,36,0.8));
  animation: rotate-continuous 20s linear infinite;
}

.points-value {
  font-size: 1.25rem;
  font-weight: 800;
  color: white;
  text-shadow: 0 2px 8px rgba(0,0,0,0.3);
}

.student-avatar-supreme {
  position: relative;
  width: 56px;
  height: 56px;
  border-radius: 50%;
  background: var(--golden-hour);
  border: 3px solid rgba(255,255,255,0.3);
  box-shadow: 
    0 8px 24px rgba(0,0,0,0.2),
    var(--glow-gold);
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 1.75rem;
  font-weight: 900;
  color: white;
  transition: all 0.4s var(--ease-bounce);
  cursor: pointer;
}

.student-avatar-supreme::before {
  content: '';
  position: absolute;
  inset: -3px;
  border-radius: 50%;
  background: var(--aurora-1);
  background-size: 200% 200%;
  opacity: 0;
  transition: opacity 0.4s;
  animation: gradient-shift 8s ease infinite;
  z-index: -1;
}

.student-avatar-supreme:hover::before {
  opacity: 1;
}

.student-avatar-supreme:hover {
  transform: scale(1.1) rotate(5deg);
  box-shadow: 
    0 12px 32px rgba(0,0,0,0.3),
    0 0 40px rgba(251,191,36,0.6);
}

.btn-logout-supreme {
  padding: var(--space-3) var(--space-6);
  background: rgba(255, 255, 255, 0.08);
  backdrop-filter: blur(20px);
  border: 1px solid rgba(255, 255, 255, 0.15);
  border-radius: var(--radius-xl);
  color: white;
  font-weight: 600;
  display: flex;
  align-items: center;
  gap: var(--space-2);
  cursor: pointer;
  transition: all 0.3s var(--ease-smooth);
}

.btn-logout-supreme:hover {
  background: rgba(239, 68, 68, 0.15);
  border-color: rgba(239, 68, 68, 0.3);
  transform: translateY(-2px);
  box-shadow: 0 8px 24px rgba(239, 68, 68, 0.2);
}

/* ─────────────────────────────────────────────────────────
   🎴 SUBJECT CARDS - DISABLED CONFLICTING STYLES
   ───────────────────────────────────────────────────────── */

