.login-container {
  min-height: 100vh;
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 20px;
  background: var(--mesh-aurora); /* Use supreme mesh background */
  position: relative;
  overflow: hidden;
}

.login-card {
  background: var(--glass-ultra-bg);
  backdrop-filter: blur(var(--glass-ultra-blur));
  -webkit-backdrop-filter: blur(var(--glass-ultra-blur));
  border: 1px solid rgba(255, 255, 255, 0.2);
  border-radius: var(--radius-3xl);
  padding: var(--space-10);
  width: 100%;
  max-width: 480px;
  box-shadow: 
    0 30px 60px rgba(0, 0, 0, 0.4),
    var(--glow-primary);
  animation: slideIn 0.8s var(--ease-bounce-soft);
  position: relative;
  z-index: 10;
}

@keyframes slideIn {
  from {
    opacity: 0;
    transform: translateY(40px) scale(0.95);
  }
  to {
    opacity: 1;
    transform: translateY(0) scale(1);
  }
}

.login-header {
  text-align: center;
  margin-bottom: var(--space-8);
}

.login-header h1 {
  font-size: clamp(2rem, 5vw, 2.75rem);
  font-weight: 900;
  background: var(--aurora-1);
  background-size: 200% 200%;
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  animation: gradient-shift 8s ease infinite;
  margin-bottom: var(--space-2);
}

.login-header p {
  color: rgba(255, 255, 255, 0.8);
  font-size: 1.1rem;
  font-weight: 300;
  text-shadow: 0 2px 10px rgba(0,0,0,0.2);
}

.login-form {
  display: flex;
  flex-direction: column;
  gap: var(--space-6);
}

.form-group {
  display: flex;
  flex-direction: column;
  gap: var(--space-2);
}

.form-group label {
  font-weight: 700;
  color: white;
  font-size: 1rem;
  letter-spacing: 0.02em;
  opacity: 0.95;
}

.glass-input {
  background: rgba(255, 255, 255, 0.08) !important;
  border: 1px solid rgba(255, 255, 255, 0.2) !important;
  border-radius: var(--radius-xl) !important;
  padding: var(--space-4) var(--space-5) !important;
  color: white !important;
  font-size: 1.1rem !important;
  transition: all 0.3s var(--ease-bounce-soft) !important;
  backdrop-filter: blur(10px);
}

.glass-input:focus {
  outline: none !important;
  background: rgba(255, 255, 255, 0.12) !important;
  border-color: rgba(255, 255, 255, 0.5) !important;
  box-shadow: 0 0 20px rgba(255, 255, 255, 0.1) !important;
  transform: translateY(-2px);
}

.form-group small {
  color: rgba(255, 255, 255, 0.6);
  font-size: 0.9rem;
  margin-top: var(--space-1);
}

.btn-aurora {
  background: var(--aurora-1);
  background-size: 200% 200%;
  color: white;
  border: none;
  padding: var(--space-4);
  border-radius: var(--radius-xl);
  font-size: 1.25rem;
  font-weight: 800;
  cursor: pointer;
  transition: all 0.4s var(--ease-bounce-soft);
  box-shadow: var(--glow-primary);
  animation: gradient-shift 5s ease infinite;
}

.btn-aurora:hover:not(:disabled) {
  transform: translateY(-4px) scale(1.02);
  box-shadow: 
    0 20px 40px rgba(0, 0, 0, 0.3),
    var(--glow-secondary);
}

.btn-aurora:active:not(:disabled) {
  transform: translateY(0) scale(0.98);
}

.btn-aurora:disabled {
  opacity: 0.5;
  cursor: not-allowed;
  filter: grayscale(1);
}

.login-footer {
  margin-top: var(--space-10);
  text-align: center;
  padding-top: var(--space-6);
  border-top: 1px solid rgba(255, 255, 255, 0.1);
}

.login-footer p {
  color: rgba(255, 255, 255, 0.5);
  font-size: 0.95rem;
  font-weight: 300;
}
